import ScrollMagic from 'scrollmagic';

export default function () {
  // ScrollMagic Scene [Array]
  var scrollMagicScenes = [];

  /**
   * ScrollMagic
   * Scene
   *  refleshScrollMagicScenes();
   * Web
   */
  var refleshScrollMagicScenes;

  /**
   * 
   * ScrollMagic
   */
  (function ($) {
    // Scroll Magic
    // init controller
    var controller = new ScrollMagic.Controller();

    // [data-scroll]
    // : https://scrollmagic.io/examples/basic/reveal_on_scroll.html
    $('[data-scroll]').each(function () {
      // 
      var attrClass = $(this).attr("class");

      // 0.9 = 90%
      var triggerHookValue = 1;

      // 
      // enter
      var durationValue = $(this).innerHeight();

      // 
      var offsetValue = 0;

      // ScrollMagic
      var scene = new ScrollMagic.Scene({
          triggerElement: this,
          triggerHook: triggerHookValue,
          duration: durationValue,
          offset: offsetValue,
          reverse: true
        })
        .on("enter", function (e) {
          // 
          $(e.target.triggerElement()).addClass("js-is-inview");
        })
        .on("leave", function (e) {
          // 
        })
        // 
        // .addIndicators({
        //   name: attrClass
        // })
        .addTo(controller);

      scrollMagicScenes.push(scene);
    });

    // ScrollMagic
    // 
    refleshScrollMagicScenes = function () {
      for (var i = 0; i < scrollMagicScenes.length; i++) {
        scrollMagicScenes[i].refresh();
      }
    }
  })(jQuery);
}
